import React, { useEffect, useState } from 'react';
import axios from "axios";
import { CoinList } from "../config/api";
import { CryptoState } from '../CryptoContext';
import { Container, createTheme, LinearProgress, makeStyles, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, ThemeProvider, Typography } from '@material-ui/core';
import { useNavigate } from 'react-router-dom';
import { Pagination } from '@material-ui/lab';

const useStyles = makeStyles(() => ({
    row: {
        backgroundColor: "#101a27",
        cursor: "pointer",
        "&:hover": {
            backgroundColor: "#091217",
        },
        fontFamily: "Roboto",
    },
    pagination: {
        "& .MuiPaginationItem-root": {
            color: "#87cefb",
        },
    },
}));

const CoinsTable = () => {

    const [coins, setCoins] = useState([]);
    const [loading, setLoading] = useState(false);
    const [search, setSearch] = useState("");
    const [page, setPage] = useState(1);
    const navigate = useNavigate();
    
    const {currency, symbol} = CryptoState();

    const fetchCoins = async () => {
        setLoading(true);
        const { data } = await axios.get(CoinList(currency));
        setCoins(data);
        setLoading(false);
    };

    useEffect(() => {
      fetchCoins();
    }, [currency])
    
    const darkTheme = createTheme({
        palette: {
          primary : {
            main: "#fff",
          },
          type: "dark",
        }
      });

      const handleSearch = () => {
        return coins.filter(
            (coin) =>
            coin.name.toLowerCase().includes(search) ||
            coin.symbol.toLowerCase().includes(search)
        );
      };

      const classes = useStyles();

  return (
    <ThemeProvider theme={darkTheme}>
        <Container style = {{ textAlign: "center"}}>
            <Typography
            variant = "h4"
            style = {{ margin:18, fontFamily: "Roboto" }}
            >
            Cryptocurrency Prices by Market Cap
            </Typography>
            <TextField 
            label="Search for a Cryptocurrency..." 
            variant="outlined"
            style = {{ marginBottom: 20, width: "80%" }}
            onChange={(e) => setSearch(e.target.value)}
            >
            </TextField>

            <TableContainer>
                {
                    loading? (
                        <LinearProgress style={{ backgroundColor: "#40E0D0"}} />
                    ) : (
                        <Table>
                            <TableHead style = {{backgroundColor: "#91bad6"}}>
                                <TableRow>
                                    {["Coin", "Price", "24h Change", "Market Cap"].map((head) => (
                                        <TableCell
                                        style = {{
                                            color: "black",
                                            fontWeight: "700",
                                            fontFamily: "Roboto",
                                        }}
                                        key = {head}
                                        align = {head === "coin"? "" : "right"}
                                        >
                                            {head}
                                        </TableCell>
                                    ))}
                                </TableRow>
                            </TableHead>

                            <TableBody>
                                {handleSearch()
                                .slice( (page-1) * 10, page * 10)
                                .map((row) => {
                                    const profit = row.price_change_percentage_24h > 0;
                                    return (
                                        <TableRow
                                        onClick={()=> navigate(`/coins/${row.id}`)}
                                        className = {classes.row}
                                        key = {row.name}
                                        >
                                            <TableCell
                                            component="th"
                                            scope="row"
                                            style={{
                                                display: "flex",
                                                gap: 15,
                                            }}
                                            >
                                            <img
                                            src={row?.image}
                                            alt={row.name}
                                            height="50"
                                            style = {{ marginBottom: 10 }}
                                            ></img>

                                            <div
                                            style = {{ display: "flex", flexDirection: "column" }}
                                            >
                                                <span
                                                style = {{ textTransform: "uppercase", fontSize: 22 }}
                                                >
                                                    { row.symbol}
                                                </span>
                                                <span
                                                style = {{ color: "darkgrey" }}
                                                >
                                                    {row.name}
                                                </span>
                                            </div>
                                            </TableCell>

                                            <TableCell align = "right">
                                                {symbol}{" "}
                                                {row.current_price.toLocaleString(undefined, {maximumFractionDigits:2})}
                                            </TableCell>

                                            <TableCell
                                            align= "right"
                                            style = {{ color: profit>0 ? "rgb(14, 203, 129)" : "red",
                                                        fontWeight: 500,
                                                    }}
                                            >
                                                {profit && "+"}
                                                {row.price_change_percentage_24h.toFixed(2)}%
                                            </TableCell>

                                            <TableCell align = "right">
                                                {symbol}{" "}
                                                {row.market_cap.toLocaleString(undefined, {maximumFractionDigits:2}).slice(0,-8)} M
                                            </TableCell>
                                        </TableRow>
                                    )
                                })}
                            </TableBody>
                        </Table>
                    )
                }
            </TableContainer>

                <Pagination
                style = {{ 
                    padding: 20,
                    width: "100%",
                    display: "flex",
                    justifyContent: "center",
                 }}
                 classes = {{ul: classes.pagination}}
                 count = {(handleSearch()?.length / 10).toFixed()}
                 onChange={(_, value) => {
                    setPage(value);
                    window.scroll(0,450);
                 }}
                >
                </Pagination>

        </Container>
    </ThemeProvider>
  )
}

export default CoinsTable